footer{
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    box-shadow: 3rem 3rem 3rem 3rem rgba(0, 0, 0, 0.7);
}

footer a{
    color: var(--color-bg);  
}
.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__social{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 3rem;
}

.footer__social a{

    padding: 0.8ram;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
    
}

.footer__copyright{
    margin-bottom: 4.5rem;
    color: var(--color-bg);
}



@media screen and (max-width: 600px) {
    
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }
    .footer__social{
        margin-bottom: 2.6rem;
    }

    }    